body {
  background: #c3c3c3;
  height: 100vh;
  margin: 0;
  padding: 1em;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.container {
  background-color: white;
  max-width: 600px;
  margin: 50px auto;
  padding: 2em 1em;
  border-radius: 15px;
}

#tamanho {
  padding: 1em 1em .3em 1em;
}

.slider {
  width: 100%;
  height: 13px;
  background: #d3d3d3; 
  outline: none;
  background-color: #a3d3cf;
  border-radius: 10px;
  margin-top:2%;
}

.slider:hover {
  opacity: 1; 
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none; 
  appearance: none;
  cursor: pointer; 
  border-radius: 10px;
  
}

.slider::-moz-range-thumb {
  width: 20px; 
  height: 20px; 
  background-color: rgb(0, 150, 136);
  cursor: pointer; 
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.5) 0px 1px 3px;
  transition-duration: 0.1s;
}

.options {
  margin: 0 auto;
  display: flex;
  padding: 4% 5%;
}

.labelsCheck {
  margin: 0px 5% 0px 2%;
  font-size: calc(10px + 1vmin);
}

.container button {
  width: 100%;
  height: 50px;
  margin: 3em auto;
  background-color: black;
  color: white;
  border: none;
  cursor: pointer;
  font-weight: bold;
}

.container button:hover {
  background-color: rgb(24, 23, 23);
}

.container .inputgera {
  width: 8%;
  height: 20px;
  margin: 1em .5em 1em .5em;
  color: black;
  border: 1px solid #c4c4c4;
}

.container .labelinputgera {
  width: auto;
  height: 20px;
  display: inline-block;
}

.container .separa {
  margin: 0px 20px 0px 15px;
  color: #c4c4c4;
}

.container .bt_senha {
  width: 21%;
  height: 30px;
  margin: 0 auto;
  background: none;
  color: black;
  border: none;
  cursor: pointer;
  float: right;
  font-weight: normal;
}

.container .bt_senha:hover {
  font-weight: bold;
  background: none;
}

.container .in_senha {
  width: 100%;
  height: auto;
  border: none;
  text-align: center;
  font-size: 1em;
  color: #333;
}
.container .sgerada {
  padding: 1em 1em 3em 1em;
}
.container .linksenha {
  width: 100%;
  border-radius: 3px;
  background-color: rgb(90, 89, 89);
  color: white;
  padding: 3em 0em;
  margin: 2% 0%;
}

@media screen and (max-width: 580px) {
  .separa {
    display: none;
  }
  .container .labelinputgera {
    width: 50%;
    height: 20px;
    display: inline-block;
  }
  .container .inputgera {
    margin: 1em 0em .5em 0em;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
